<template>
  <div>
    <div class="container">
    </div>
    <div class="box">
      <div style="width: 20%;padding-top: 8%">
        <h2>用户登录</h2>
        <div class="box">
          <input type="text" v-model="username" size="large" placeholder="请输入邮箱" autofocus/>
        </div>
        <div class="box">
          <input type="password" v-model="password" size="large" placeholder="请输入密码"/>
        </div>
      </div>
    </div>
    <div class="box">
      <button type="primary" v-on:click="userLogin" size="large">登录</button>
      <a href="/register">
        <button type="success" size="large" style="margin-left: 10px">注册</button>
      </a>
      <a href="#">
      <button type="default" v-on:click=forgetPassword() size="large" style="margin-left: 10px">忘记密码</button>
      </a>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')
export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    send: function () {
      axios({
        method: 'post',
        url: '/login',
        data: {'mail': this.username, 'password': this.password},
        header: {
          // 如果写成contentType会报错
          'Content-Type': 'application/json'
        }
      }).then(function (resp) {
        console.log(resp.data)
        console.log(resp.data['uid'])
        if (resp.data['uid'] !== '') {
          alert('登录成功！')
        }
      }).catch(resp => {
        console.log('请求失败：' + resp.status + ',' + resp.statusText)
      })
    },

    checkMail: function () {
      var re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/
      if (re.test(this.user.name)) {
        return true
      } else {
        return false
      }
    },

    // 跳转到的登录页面
    userLogin: function () {
      var iftrue = this.checkMail()
      if (iftrue) {
        this.send()
      } else {
        alert('邮箱格式不正确，请重新输入！')
      }
    }
  }
}
</script>

<style scoped>
  .container{
    width: 100%;
    margin: 0 auto;
  }
  .box{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
  }
</style>



